.container {
  display: none;
}
.hide {
  display: none;
}
.menuButton {
  cursor: pointer;
  margin-left: 16px;
}

@media screen and (max-width: 850px) {
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100%;
  }
}
